<template>
    <div class="h5-my-blog">
        <van-nav-bar
            title="我的博客"
            left-text="返回"
            left-arrow
            fixed
            @click-left="$router.go(-1)"
        />
        <van-sticky :offset-top="46">
            <van-notice-bar mode="closeable">
                请向左滑动进行操作! 请向左滑动进行操作! 请向左滑动进行操作!
            </van-notice-bar>
        </van-sticky>
        <transition-group tag="ul" name="fade">
            <li v-for="item in $store.state.blog.myBlogs" :key="item._id">
                <van-swipe-cell>
                    <MyPageBlogItem :blog="item" />
                    <template #right>
                        <van-button
                            square
                            text="删除"
                            type="danger"
                            class="delete-button"
                            @click.stop="handleDelete(item._id)"
                        />
                        <van-button
                            square
                            text="编辑"
                            type="primary"
                            @click.stop="handleEdit(item._id)"
                            class="delete-button"
                        />
                    </template>
                </van-swipe-cell>
            </li>
        </transition-group>
        <EmtyTips v-show="!$store.state.blog.myBlogs.length" />
    </div>
</template>

<script>
export default {
    mounted() {
        this.$store.dispatch("getMyBlog");
        setTimeout(() => {
            console.log();
        }, 2000);
    },
    methods: {
        async handleDelete(id) {
            this.$dialog
                .confirm({
                    title: "删除",
                    message: "此操作将永久删除该内容, 是否继续?"
                })
                .then(async () => {
                    let msg = await this.$store.dispatch("deleteBlog", id);
                    let { ok } = msg;
                    if (ok === 1) {
                        this.$notify({
                            type: "success",
                            message: "删除成功!"
                        });
                        this.$store.commit("DELETEMYBLOG", id);
                    } else {
                        throw new Error("删除失败");
                    }
                })
                .catch(e => {
                    this.$notify({
                        type: "warning",
                        message:
                            e.toString() === "cancel"
                                ? "已取消删除"
                                : e.toString()
                    });
                    return;
                });
        },
        handleEdit() {
            this.$notify({
                type: "warning",
                message: "手机等小屏设备暂不支持!"
            });
        }
    }
};
</script>


<style lang="scss" scoped>
.h5-my-blog {
    width: 100%;
    font-size: 16px;
    box-sizing: border-box;
    padding-top: 46px;
}
.delete-button {
    height: 100%;
}
</style>